				}else{
					this.bell_gubun = 'mdi-bell-ring'
				}
				console.log(this.bell_items);
			});
		},
		fnc_Message_Confirm(){
			if(typeof this.bell_items[Number(this.bell_index)] == 'undefined'){
				if(this.MessageModal == true){
					this.MessageModal = false
				}
				this.bell_index = '0'
				this.bell_gubun = 'mdi-bell'
			}else{
				if(this.MessageModal == false){
					this.MessageModal = true
				}
				this.msg_id = this.bell_items[Number(this.bell_index)].clm_message_id
				this.msg_sender_name = this.bell_items[Number(this.bell_index)].clm_sender_user_name
				this.msg_sender_datetime = this.bell_items[Number(this.bell_index)].clm_sender_datetime
				this.msg_title = this.bell_items[Number(this.bell_index)].clm_message_title
				this.msg_text = this.bell_items[Number(this.bell_index)].clm_message_text

				axios.post('/back/Common/MSGConfirm', null, { params: {
					msg_id:this.msg_id
				}})
