		fnc_payment_click(){
			this.paymentModal =true;
			this.search = '';
			this.getPaymentModalList();
		},
		getPaymentModalList(){
			const vm = this
			this.loading2 = true
			let items = axios.get('/back/Popup/StockAllPayment',{
				params: {
			        search: encodeURI(this.search)
					}
				}).then(
				response => {
					let items = response.data	
					vm.loading2 = false
					for(var x=0;x<items.length;x++ ) {
						if(x !=0 && items[x-1].clm_request_delivery_datetime == items[x].clm_request_delivery_datetime) { 
							// 이전 행의 데이터값과 기존 행의 데이터값이 같을 때 rowspan 값은 1이 되어야 함 (그래야 추가 병합이 일어나지 않음)
							items[x].rowspan = 'y'
						}
						if(x !=0 && items[x-1].clm_client_name == items[x].clm_client_name&& items[x-1].clm_request_delivery_datetime == items[x].clm_request_delivery_datetime) { 
							items[x].clientspan = 'y'
						}
						
					}
					this.m_payment_items = items
					console.log(items)
				}
			)
		},
		getList() {
			const vm = this
			this.loading = true		
			// this.save_yn='Y';	
			return new Promise((resolve, reject) => {
				const { sortBy, sortDesc, page, itemsPerPage } = this.options
				axios.get('/back/MOB001001/List',{
						params: {
							gubun: this.gubun_id,
							keyword: encodeURI(this.keyword),
							start_date: this.start_date,
							end_date: this.end_date,
							page: page,
							mob:'mob001',
							PerPage: itemsPerPage,
							clm_user: this.$session.get('user_id'),
						}
					}).then(
					response => {
						if (response.status == '200') {
							setTimeout(() => {
								// this.save_yn='N';
								this.loading = false
								this.document =  response.data
							}, 100)
						}else{
							// this.save_yn='N';
							this.save_yn='N';
							this.loading = false
							Swal.fire({
								title:'서버가 불안정합니다.\n다시 조회해주세요.',
								icon: 'error'
							});
							this.document =[];
						}
					},
				)
			})
		}, 
		multiModalOpen(){
			this.HalfMultiModal=true;
		},
		blockModalOpen(){
			this.BlockHalfModal=true;
		},
		async blockitemsChange(value){
			var error_yn='N';
			if(Number(this.BlockHalfItems[value].clm_full_count)<Number(this.BlockHalfItems[value].clm_order_count) ){
				Swal.fire({
					title:'자재 사용 수량이 가용수량을 초과했습니다.',
					icon: 'error'
				});
				error_yn='Y';
				// this.BlockHalfItems[index].clm_order_count=Number(this.BlockHalfItems[index].clm_full_count);
			}else if(Number(this.BlockHalfItems[value].clm_order_count)<1){
				Swal.fire({
					title:'자재 사용 수량이 정상적이지 않습니다.',
					icon: 'error'
				});
				error_yn='Y';
				// this.BlockHalfItems[index].clm_order_count=Number(this.BlockHalfItems[index].clm_full_count);
			}else if(Number(this.BlockHalfItems[value].clm_order_count)%1 !=0){
				Swal.fire({
					title:'자재 사용 수량이 정상적이지 않습니다.',
					icon: 'error'
				});
				error_yn='Y';
				// this.BlockHalfItems[index].clm_order_count=Number(this.BlockHalfItems[index].clm_full_count);
			}else if(this.BlockHalfItems[value].clm_order_count=='' || typeof this.BlockHalfItems[value].clm_order_count=='undefined'){
				error_yn='Y';
				// this.BlockHalfItems[index].clm_order_count=Number(this.BlockHalfItems[index].clm_full_count);
			}	
			if(error_yn== 'Y' && this.BlockHalfItems.length>0){
				var sum_multi =0;
				for(var x=0;x<this.BlockHalfItems.length;x++){
					if(x!=value){
						sum_multi= sum_multi+Number(this.BlockHalfItems[x].clm_order_count);
					}
				}
				this.BlockHalfItems[value].clm_order_count=Number(this.HalfItems[1].clm_m_material_count) -sum_multi ;
				return;
			}
			var sum =0;
			var useable_sum=0;
			for(var x=0; x<this.BlockHalfItems.length;x++){
				sum=sum+Number(String(this.BlockHalfItems[x].clm_order_count).replace(/,/gi, ""));
				useable_sum=useable_sum+Number(String(this.BlockHalfItems[x].clm_full_count).replace(/,/gi, ""));
			}
			//3배 가능
			if(Number(this.HalfItems[0].clm_m_usable_count)*3 < sum && this.HalfItems[0].clm_m_usable_count!='' && Number(this.HalfItems[0].clm_m_usable_count)!=0){
				//2번째 자재 깎음
				// var count = Number(sum)-index;
				var count = sum-Number(this.HalfItems[0].clm_m_usable_count)*3 ;
				for(var x=this.BlockHalfItems.length-1;x>=0;x--){
