		ChecksaveMachine(){
			var id= '';
			var name ='';
			for(var x=0;x<this.machine_document_1.length;x++){
				if(this.machine_document_1[x].clm_check_yn=='Y'){
					id=id+this.machine_document_1[x].clm_machine_id+',';
					name=name+this.machine_document_1[x].clm_machine_name+',';
				}
			}
