					id=id+this.machine_document_3[x].clm_machine_id+',';
					name=name+this.machine_document_3[x].clm_machine_name+',';
				}
			}
			
			if(id == '' || name ==''){
				Swal.fire({
					title:'선택된 설비가 없습니다.',
					icon: 'error'
				});
				return;
			}
			var count = id.split(',').length - 1;
			if(count!=1){
				Swal.fire({
					title:'설비는 1개만 선택 가능합니다.',
					icon: 'error'
				});
				return;
			}
			id = id.slice(0, -1);
			name =name.slice(0, -1);

			if( this.cut_half_gubun=='assembly'){
				this.m_assemblylocationid =id;
				this.m_assemblylocationname =name;
			}else if(this.cut_half_gubun=='cut'){
