		async StockProductRegPlan(){
			if(this.loading_yn=='Y'){
				alert('저장중입니다.잠시만 기다려주세요.');
				return;
			}
			var Error_Sum_less='N';
			var Error_Mat_LotNo = 'N';
			this.loading_yn='Y';
			var Error_Mat_Count = 'N';
			var Error_Mat_Count2 = 'N';
			var Error_Sum_over = 'N';
			var materialData = '';
			var error_use='N';
			var cnt =0;

			if(this.clm_m2_product_id == '' || this.clm_m2_product_name == ''){
				alert('제품이 선택되지 않았습니다.');
				this.loading_yn='N';
				return;
			}
			if(this.clm_m2_product_count == '' || Number(this.clm_m2_product_count) < 1){
				alert('제품 수량이 정상적이지 않습니다.');
				this.loading_yn='N';
				return;
			}
			if(this.clm_m_assign_manager_name == ''){
				alert('파트장을 선택해주시기 바랍니다.');
				this.loading_yn='N';
				return;
			}
			if(this.clm_m2_product_location_id == ''){
				alert('제품창고가 선택되지 않았습니다.');
				this.loading_yn='N';
				return;
			}
			if(this.clm_m2_product_location_position_id == ''){
				alert('제품위치가 선택되지 않았습니다.');
				this.loading_yn='N';
				return;
			}
			if(this.stockItems.length == 0){
				alert('제품을 선택해주시기 바랍니다.');
				this.loading_yn='N';
				return;
			}
			if(this.stockItems.length > 0){
				for(var i=0; i<this.stockItems.length; i++){
				var tmp_material_order_use_total=0;//동일 부품 사용량
				var tmp_material_cube_size =0;//동일 자재 사용량
				var tmp_material_count =0;//동일 부품에 자재 있는 개수
					if(materialData != ''){
						materialData += '▒';
					}
					for(var j=0; j<this.stockItems.length; j++){
						if(this.stockItems[i].clm_part_id==this.stockItems[j].clm_part_id){
							tmp_material_order_use_total = Number(tmp_material_order_use_total) + Number(this.stockItems[j].clm_use_count);
						}
						if(this.stockItems[i].clm_material_lotno== this.stockItems[j].clm_material_lotno){
							tmp_material_cube_size = Number(tmp_material_cube_size) + Number(this.stockItems[j].clm_use_count);
						}
						if(this.stockItems[i].clm_part_id==this.stockItems[j].clm_part_id && this.stockItems[i].clm_material_lotno!=''){
							tmp_material_count = Number(tmp_material_count) + 1;
						}
					}
					if(this.stockItems[i].clm_material_lotno == ''){
						Error_Mat_LotNo = 'Y';
					}
					if(this.stockItems[i].clm_material_count == '' ){
						this.stockItems[i].clm_material_count=0;
					}
					
					if((Number(tmp_material_order_use_total)!=0 ||Number(this.stockItems[i].clm_use_count)!=0 ) && Number(tmp_material_order_use_total)!=Number(this.stockItems[i].clm_need_count) && this.stockItems[i].clm_same_count_yn=='Y'){
						// Error_Same_countYN = 'Y';
						alert(this.stockItems[i].clm_part_name+'은(는) 필요수량과 사용수량이 동일해야합니다.');
						this.loading_yn='N';
						return;
					}
					if(Number(this.stockItems[i].clm_use_count) < 0 || Number(this.stockItems[i].clm_use_count)% 1!= 0){
						Error_Mat_Count = 'Y';
					}
					if(Number(this.stockItems[i].clm_material_full_count) < Number(this.stockItems[i].clm_use_count)){
						Error_Mat_Count2 = 'Y';
					}
					
					if(tmp_material_order_use_total > Number(this.stockItems[i].clm_need_count) && this.stockItems[i].clm_same_count_yn=='Y'){
						Error_Sum_over = 'Y';
					}else if(tmp_material_order_use_total < Number(this.stockItems[i].clm_need_count)&& this.stockItems[i].clm_same_count_yn=='Y'){
						Error_Sum_less = 'Y';
					}
					//사용체크
					if(this.stockItems[i].clm_material_id!=''){
						await axios.post('/back/Common/UseCheck', null, { params: {
						clm_joborder_id: this.clm_m2_joborder_id,
						clm_material_id:this.stockItems[i].clm_material_id,
						clm_material_count:this.stockItems[i].clm_material_full_count,
						clm_material_order_id:this.stockItems[i].clm_material_order_id,
						clm_order_income_seq: this.stockItems[i].clm_order_income_seq,
						clm_income_material_seq: this.stockItems[i].clm_income_material_seq,
						clm_material_leftover_seq:this.stockItems[i].clm_material_leftover_seq			
						}})
						.then(response => {
							if (response.status == '200') {
								if(response.data!='N'){
									cnt= i;
									error_use=response.data;
								}
							}
						})
						.catch(error => {
							console.log(error)
						})
					}
				materialData += this.stockItems[i].clm_material_lotno + '▦' + this.stockItems[i].clm_material_order_id + '▦' + this.stockItems[i].clm_order_income_seq + '▦' + this.stockItems[i].clm_income_material_seq + '▦' + this.stockItems[i].clm_material_leftover_seq + '▦' + this.stockItems[i].clm_material_id + '▦' + this.stockItems[i].clm_material_full_count + '▦' + this.stockItems[i].clm_use_count+ '▦' + this.stockItems[i].clm_part_id + '▦' + tmp_material_cube_size + '▦' + tmp_material_count;
				}
