		changeMultiCount(value){
			var error_yn='N';
			if(Number(this.MultiHalfItems[value].clm_full_count)<Number(this.MultiHalfItems[value].clm_order_count) ){
				alert('자재 사용 수량이 가용수량을 초과했습니다.');
				error_yn='Y';
			}else if(Number(this.MultiHalfItems[value].clm_order_count)<1){
				alert('자재 사용 수량이 정상적이지 않습니다.');
				error_yn='Y';
			}else if(Number(this.MultiHalfItems[value].clm_order_count)%1 !=0){
				alert('자재 사용 수량이 정상적이지 않습니다.');
				error_yn='Y';
			}else if(this.MultiHalfItems[value].clm_order_count=='' || typeof this.MultiHalfItems[value].clm_order_count=='undefined'){
				error_yn='Y';
			}
			if(error_yn== 'Y' && this.MultiHalfItems.length>0){
				var sum_multi =0;
				for(var x=0;x<this.MultiHalfItems.length;x++){
					if(x!=value){
						sum_multi= sum_multi+Number(this.MultiHalfItems[x].clm_order_count);
					}
				}
				this.MultiHalfItems[value].clm_order_count=Number(this.HalfItems[0].clm_m_material_count)-sum_multi;
				return;
			}
			var half_sum =0;
			for(var x=0;x<this.MultiHalfItems.length;x++){
				half_sum = half_sum +Number(this.MultiHalfItems[x].clm_order_count );
			}
			this.HalfItems[0].clm_m_material_count= half_sum;
