						console.log('자재 수량 변경 - 3배 가능');
						//2번째 자재 깎음
						var count= sum -Number(this.HalfItems[0].clm_m_material_count)*3;
						for(var x=0; x<this.BlockHalfItems.length;x++){
							this.BlockHalfItems[x].clm_order_count = Number(this.BlockHalfItems[x].clm_full_count);
						}
						for(var x=this.BlockHalfItems.length-1;x>=0;x--){
							if(Number(this.BlockHalfItems[x].clm_full_count)<=count && count!=0){
								count= count-Number(this.BlockHalfItems[x].clm_full_count);
								this.BlockHalfItems.splice(x,1);
