									half_sum =half_sum +Number(this.MultiHalfItems[x].clm_order_count);
								}
								//1번째 자재 깎음
								var count = half_sum-(index/3);
								for(var x=this.MultiHalfItems.length-1;x>=0;x--){
									if(Number(this.MultiHalfItems[x].clm_order_count)<=count && count!=0){
										count= count-Number(this.MultiHalfItems[x].clm_order_count);
