									}else if(Number(this.BlockHalfItems[x].clm_full_count)>count && count!=0){
										this.BlockHalfItems[x].clm_order_count = Number(this.BlockHalfItems[x].clm_full_count)- count;
										count=0;
									}
								}	
							}
						}
					}
					var half_sum=0;
					var half_useable_sum=0;
					var block_sum=0;
					var block_useable_sum=0;
					for(var x=0;x<this.MultiHalfItems.length;x++){
