					}
					for(var x=0;x<this.BlockHalfItems.length;x++){
						block_sum = block_sum+Number(String(this.BlockHalfItems[x].clm_order_count).replace(/,/gi, ""));
						block_useable_sum = block_useable_sum+Number(String(this.BlockHalfItems[x].clm_full_count).replace(/,/gi, ""));
					}
					this.HalfItems[0].clm_m_material_count= Number(half_sum);
					if((this.m_product_type.includes('MDF') || this.m_product_type.includes('합판')) && this.PlywoodModal==true){
						this.m_half_product_count= Number(half_sum);
					}
