							this.ReleaseCountItems[index].clm_release_count =Number(this.ReleaseCountItems[index].clm_release_client_order_count)-sum_stock_count;
							//this.ReleaseCountItems[index].clm_before_release_count =this.ReleaseCountItems[index].clm_release_count ;
