		halfcountChange(value){
			if(Number(this.MultiHalfItems[value].clm_order_count)>Number(this.MultiHalfItems[value].clm_full_count)  && this.MultiHalfItems.length>0){
				var sum_multi =0;
				for(var x=0;x<this.MultiHalfItems.length;x++){
					if(x!=value){
						sum_multi= sum_multi+Number(this.MultiHalfItems[x].clm_order_count);
					}
				}
				this.MultiHalfItems[value].clm_order_count=Number(this.HalfItems[0].clm_m_material_count)-sum_multi;
				return;
			}
			var sum= 0;
			var usable_sum =0;
			for(var x=0;x<this.MultiHalfItems.length;x++){
				sum= sum+Number(this.MultiHalfItems[x].clm_order_count);
				usable_sum= usable_sum+Number(this.MultiHalfItems[x].clm_full_count);
			}
			var block_sum =0;
			for(var x=0;x<this.BlockHalfItems.length;x++){
				block_sum= block_sum+Number(this.BlockHalfItems[x].clm_full_count);
			}
			this.HalfItems[0].clm_m_material_count=sum;
			this.HalfItems[0].clm_m_material_count=usable_sum;
			if((this.m_product_type.includes('MDF') || this.m_product_type.includes('합판')) && this.PlywoodModal==true){
				this.m_half_product_count=usable_sum;
			}
			this.HalfItems[1].clm_m_usable_count= block_sum;
			if(this.HalfItems.length==2){
				if( this.HalfItems[0].clm_m_material_name!=''&& this.HalfItems[1].clm_m_material_name!=''){
					if(Number(this.HalfItems[0].clm_m_material_count)*3 <=block_sum && this.HalfItems[0].clm_m_usable_count!='' && Number(this.HalfItems[0].clm_m_usable_count)!=0){
						//3배 가능
						this.HalfItems[1].clm_m_material_count=Number(this.HalfItems[0].clm_m_material_count)*3
						var count = Number(block_sum)-Number(this.HalfItems[0].clm_m_material_count)*3;
						for(var x=0; x<this.BlockHalfItems.length;x++){
									this.BlockHalfItems[x].clm_order_count = Number(this.BlockHalfItems[x].clm_full_count);
								}
						for(var x=this.BlockHalfItems.length-1;x>=0;x--){
							if(Number(this.BlockHalfItems[x].clm_full_count)<=count && count!=0){
								count= count-Number(this.BlockHalfItems[x].clm_full_count);
								this.BlockHalfItems.splice(x,1);
							}else if(Number(this.BlockHalfItems[x].clm_full_count)>count && count!=0){
								this.BlockHalfItems[x].clm_order_count = Number(this.BlockHalfItems[x].clm_full_count)-count;
								count=0;
							}
						}
					}else{
						//3배 불가능
						var index =Number(this.HalfItems[1].clm_m_usable_count);
					
						for(let i=0;i<3;i++){
							if(index%3!=0){
								index=index-1;
							}else{
								break;
							}
						}
						if(Number(this.HalfItems[0].clm_m_material_count) >= index/3){
							//1번째 자재 깎음
							this.HalfItems[0].clm_m_material_count=index/3;
							if((this.m_product_type.includes('MDF') || this.m_product_type.includes('합판')) && this.PlywoodModal==true){
								this.m_half_product_count=index/3;
							}
							this.HalfItems[1].clm_m_material_count=index;
							var count=Number(this.HalfItems[0].clm_m_usable_count)-(index/3);
							// for(var x=0;x<this.MultiHalfItems.length;x++){
							// 	this.MultiHalfItems[x].clm_order_count=Number(this.MultiHalfItems[x].clm_full_count);
							// }
							for(var x=this.MultiHalfItems.length-1;x>=0;x--){
								if(count>0 && count>=Number(this.MultiHalfItems[x].clm_order_count)){
									count =count -Number(this.MultiHalfItems[x].clm_order_count);
									this.MultiHalfItems.splice(x,1);
								}else if(count==0){
									break;
								}else if(count>0 && count<Number(this.MultiHalfItems[x].clm_order_count)){
									this.MultiHalfItems[x].clm_order_count=Number(this.MultiHalfItems[x].clm_order_count)-count;
									count =0;
								}
							}
							//2번째 자재 깎음
							var sum=0;
							var usable_count=0;
							for(var x=0;x<this.BlockHalfItems.length;x++){
								sum= sum+ Number(this.BlockHalfItems[x].clm_order_count);
								usable_count= usable_count+ Number(this.BlockHalfItems[x].clm_full_count);
								this.BlockHalfItems[x].clm_order_count=Number(this.BlockHalfItems[x].clm_full_count);
							}
							var count = Number(usable_count)-index;
							// var count = Number(this.HalfItems[1].clm_m_usable_count)-index;

							for(var x=this.BlockHalfItems.length-1;x>=0;x--){
								if(Number(this.BlockHalfItems[x].clm_full_count)<=count && count!=0){
									// this.BlockHalfItems[x].clm_order_count = Number(this.BlockHalfItems[x].clm_full_count);
									count= count-Number(this.BlockHalfItems[x].clm_full_count);
									this.BlockHalfItems.splice(x,1);
								}else if(Number(this.BlockHalfItems[x].clm_full_count)>count && count!=0){
									this.BlockHalfItems[x].clm_order_count = Number(this.BlockHalfItems[x].clm_full_count)-count;
									count=0;
								}
							}
						}
						var half_sum=0;
						var half_useable_sum=0;
						var block_sum=0;
						var block_useable_sum=0;
						for(var x=0;x<this.MultiHalfItems.length;x++){
							half_sum = half_sum+Number(String(this.MultiHalfItems[x].clm_order_count).replace(/,/gi, ""));
							half_useable_sum = half_useable_sum+Number(String(this.MultiHalfItems[x].clm_full_count).replace(/,/gi, ""));
						}
						for(var x=0;x<this.BlockHalfItems.length;x++){
							block_sum = block_sum+Number(String(this.BlockHalfItems[x].clm_order_count).replace(/,/gi, ""));
							block_useable_sum = block_useable_sum+Number(String(this.BlockHalfItems[x].clm_full_count).replace(/,/gi, ""));
						}
						this.HalfItems[0].clm_m_material_count= Number(half_sum);
						if((this.m_product_type.includes('MDF') || this.m_product_type.includes('합판')) && this.PlywoodModal==true){
							this.m_half_product_count= Number(half_sum);
						}
						this.HalfItems[0].clm_m_usable_count= Number(half_useable_sum);
						this.HalfItems[1].clm_m_material_count= Number(block_sum);
						this.HalfItems[1].clm_m_usable_count= Number(block_useable_sum);
						
						var cnt2 = this.MultiHalfItems.length-1;
						var cnt = this.BlockHalfItems.length-1;

						if(cnt2==-1){
							this.HalfItems[0].lotno= '';
							this.HalfItems[0].clm_m_material_name = '';
						}else if(cnt2==0 && this.MultiHalfItems.length>0){
							this.HalfItems[0].lotno= this.MultiHalfItems[0].lotno;
							this.HalfItems[0].clm_m_material_name = this.MultiHalfItems[0].clm_material_name;
						}else if (cnt2>0 && this.MultiHalfItems.length>0){
							this.HalfItems[0].lotno= this.MultiHalfItems[0].lotno+' 외 '+cnt2 +'개';
							this.HalfItems[0].clm_m_material_name = this.MultiHalfItems[0].clm_material_name+' 외 '+cnt2 +'개';
						}
						if(cnt==-1){
							this.HalfItems[1].lotno= '';
							this.HalfItems[1].clm_m_material_name = '';
						}else if(cnt==0 && this.BlockHalfItems.length>0){
							this.HalfItems[1].lotno= this.BlockHalfItems[0].lotno;
							this.HalfItems[1].clm_m_material_name = this.BlockHalfItems[0].clm_material_name;
						}else if (cnt>0 && this.BlockHalfItems.length>0){
							this.HalfItems[1].lotno= this.BlockHalfItems[0].lotno+' 외 '+cnt +'개';
							this.HalfItems[1].clm_m_material_name = this.BlockHalfItems[0].clm_material_name+' 외 '+cnt +'개';
						}
					}
				}
			}
		},
