		async ProductSave(){
			if(this.loading_yn=='Y'){
				alert('저장중입니다.잠시만 기다려주세요.');
				return;
			}
			this.loading_yn='Y';
			var Error_Mat_LotNo = 'N';
			var Error_Mat_Count = 'N';
			var Error_Mat_Count2 = 'N';
			var Error_Sum_less = 'N';
			var Error_Sum_over = 'N';
			var materialData = '';
			var error_use='N';
			var cnt =0;
			if(this.productItems.length > 0){
				for(var i=0; i<this.productItems.length; i++){
					var tmp_material_order_use_total=0;//동일 부품 사용량
					var tmp_material_cube_size =0;//동일 자재 사용량
					if(materialData != ''){
						materialData += '▒';
					}
					for(var j=0; j<this.productItems.length; j++){
						if(this.productItems[i].clm_part_id==this.productItems[j].clm_part_id){
							tmp_material_order_use_total = Number(tmp_material_order_use_total) + Number(this.productItems[j].clm_use_count);
						}
						if(this.productItems[i].clm_material_lotno== this.productItems[j].clm_material_lotno){
							tmp_material_cube_size = Number(tmp_material_cube_size) + Number(this.productItems[j].clm_use_count);
						}
					}
					if(this.productItems[i].clm_material_lotno == ''){
						Error_Mat_LotNo = 'Y';
					}
					if(this.productItems[i].clm_material_count == '' ){
						this.productItems[i].clm_material_count=0;
					}
					if(this.productItems[i].clm_material_name !='' && Number(tmp_material_order_use_total)!=Number(this.productItems[i].clm_need_count) && this.productItems[i].clm_same_count_yn=='Y'){
						alert(this.productItems[i].clm_part_name+'은(는) 필요수량과 사용수량이 동일해야합니다.');
						this.loading_yn='N';
						return;
					}
					if(Number(this.productItems[i].clm_use_count) < 0 || Number(this.productItems[i].clm_use_count)% 1!= 0){
						Error_Mat_Count = 'Y';
					}
					if(Number(this.productItems[i].clm_material_full_count) < Number(this.productItems[i].clm_use_count)){
						Error_Mat_Count2 = 'Y';
					}
					
					if(tmp_material_order_use_total > Number(this.productItems[i].clm_need_count) && this.productItems[i].clm_same_count_yn=='Y'){
						Error_Sum_over = 'Y';
					}else if(tmp_material_order_use_total < Number(this.productItems[i].clm_need_count)&& this.productItems[i].clm_same_count_yn=='Y'){
						Error_Sum_less = 'Y';
					}
					//사용체크
					if(this.productItems[i].clm_material_id!=''){
						await axios.post('/back/Common/UseCheck', null, { params: {
						clm_joborder_id: this.m_joborder_id,
						clm_material_id: this.productItems[i].clm_material_id,
						clm_material_count: this.productItems[i].clm_material_full_count,
						clm_material_order_id:this.productItems[i].clm_material_order_id,
						clm_order_income_seq: this.productItems[i].clm_order_income_seq,
						clm_income_material_seq: this.productItems[i].clm_income_material_seq,
						clm_material_leftover_seq:this.productItems[i].clm_material_leftover_seq			
						}})
						.then(response => {
							if (response.status == '200') {
								if(response.data!='N'){
									cnt= i;
									error_use=response.data;
								}
							}
						})
						.catch(error => {
							console.log(error)
						})
					}
					materialData += this.productItems[i].clm_material_lotno + '▦' + this.productItems[i].clm_material_order_id + '▦' + this.productItems[i].clm_order_income_seq + '▦' + this.productItems[i].clm_income_material_seq + '▦' + this.productItems[i].clm_material_leftover_seq + '▦' + this.productItems[i].clm_material_id + '▦' + this.productItems[i].clm_material_full_count + '▦' + this.productItems[i].clm_use_count+ '▦' + this.productItems[i].clm_part_id + '▦' + tmp_material_cube_size + '▦' + this.productItems[i].clm_load_repository + '▦' + this.productItems[i].clm_load_repository_position;
				}
			}
			if(error_use=='Y'){
				alert(this.productItems[cnt].clm_material_lotno +'은(는) 이미 사용된 자재입니다.');
				this.loading_yn='N';
				return;
			}else if(error_use=='D'){
				alert(this.productItems[cnt].clm_material_lotno +'은(는) 삭제된 자재입니다.');
				this.loading_yn='N';
				return;
			}else if(error_use=='U'){
				alert(this.productItems[cnt].clm_material_lotno +'은(는) 수정된 자재입니다.');
				this.loading_yn='N';
				return;
			}
			if(Error_Mat_Count == 'Y'){
				alert('수량이 정상적이지 않은 자재가 있습니다.');
				this.loading_yn='N';
				return;
			}
			if(Error_Mat_Count2 == 'Y'){
				alert('자재가용수량을 초과한 자재가 있습니다.');
				this.loading_yn='N';
				return;
			}
			var mapping_yn = 'N';
			if(Error_Sum_over == 'Y'){
				alert('필요수량보다 현재고수량이 많은 부품이 있습니다.');
				this.loading_yn='N';
				return;
			}

			// if(this.clm_m2_request_delivery_date != '' && this.clm_m2_request_delivery_time != ''){
			// 	this.clm_m2_request_delivery_datetime = this.clm_m2_request_delivery_date + ' ' + this.clm_m2_request_delivery_time+':00:00';
			// }else{
			// 	this.clm_m2_request_delivery_datetime = '';
			// }
				//back/MOB001001/ProductSave
			axios.post('/back/SCR003009/ProductReg3', null, { params: {
				clm_client_order_id:this.m_client_order_id,
				clm_joborder_id:this.m_joborder_id,
				clm_assign_manager_id :this.m_user_id,
				clm_client_id: this.m_client_id,
				clm_product_id: this.m_product_id,
				clm_product_count: Number(this.m_product_count),
				clm_produce_finish_datetime:this.m_delivery_request_datetime,
				clm_request_delivery_datetime:this.m_delivery_request_datetime,
				clm_product_location_id: this.m_product_repository_location_id,
				clm_product_location_position_id: this.m_product_repository_location_position_id,
				clm_user: this.user_id,
				clm_gubun:'mob',
				clm_status:'생산계획',
				clm_assembly_user: '',
				clm_assembly_location:'',
				materialData: materialData,
				mapping_yn : mapping_yn
			}})
			.then(response => {
				if (response.status == '200') {
					this.loading_yn='N';
					this.DetailModal = false;
					Swal.fire({
						title: '정상적으로 등록 되었습니다.',
						icon: 'success'
					});
					this.selected_items=[];
					this.selected_items2=[];
					this.productItems = [{}];
					this.productItems.splice(0,1);
					this.GroupItems=[{}];
					this.GroupItems.splice(0,1);
					this.stockItems=[{}];
					this.stockItems.splice(0,1);
					this.getList();
				}
			})
			.catch(error => {
				console.log(error)
			})
		},
