		addCutPage(){
			if(this.max_cut_page==this.current_cut_page && this.current_cut_page!=3){
				//페이지 추가
				this.max_cut_page=Number(this.max_cut_page)+1;
				this.current_cut_page=Number(this.current_cut_page)+1;
			}else if(this.max_cut_page!=this.current_cut_page&& this.current_cut_page!=3){
				//다음페이지
				this.current_cut_page=Number(this.current_cut_page)+1;
			}
			setTimeout(() => {
				var obj = document.getElementsByName("use_gubun");
				var obj2 = document.getElementsByName("use_gubun2");
				var obj3 = document.getElementsByName("use_gubun3");
				var cnt = 0;
				if(this.current_cut_page==1){
					if(this.clm_m3_radio_gubun=='use'){
						cnt =0;
					}else if(this.clm_m3_radio_gubun=='loss'){
						cnt =1;
					}else{
						cnt =2;
					}
					for (var i = 0; i < obj.length; i++) {
						if(i==cnt){
							obj[i].checked = true;
						}else{
							obj[i].checked = false;
						}
