			var tmp_name ='';
			if(this.m_width_yn=='Y' && this.m_height_yn=='N' && this.m_thickness_yn=='N'){
				tmp_name='W';
			}else if(this.m_width_yn=='N' && this.m_height_yn=='Y' && this.m_thickness_yn=='N'){
				tmp_name='H';
			}else if(this.m_width_yn=='N' && this.m_height_yn=='N' && this.m_thickness_yn=='Y'){
				tmp_name='T';
			}else if(this.m_width_yn=='N' && this.m_height_yn=='Y' && this.m_thickness_yn=='Y'){
				tmp_name='HT';
			}else if(this.m_width_yn=='Y' && this.m_height_yn=='Y' && this.m_thickness_yn=='Y'){
				tmp_name='WHT';
			}else if(this.m_width_yn=='Y' && this.m_height_yn=='Y' && this.m_thickness_yn=='N'){
				tmp_name='WH';
			}else if(this.m_width_yn=='Y' && this.m_height_yn=='N' && this.m_thickness_yn=='Y'){
				tmp_name='WT';
			}else{
				tmp_name='';
				alert('설정 불가능한 범위입니다.');
				return;
			}
			// alert(tmp_name)
			if(tmp_name!=''){
				Swal.fire({
					title:"모든 페이지가 초기화됩니다. 진행하시겠습니까?",
					text:  '',
					icon: 'question',
