				allowOutsideClick: false,
				confirmButtonColor: '#3085d6',
				cancelButtonColor: '#d33',
				confirmButtonText: '예',
				cancelButtonText: '아니요'
			}).then((result) => {
				if (result.isConfirmed) {
					this.m_half_product_name='';
					this.m_half_id='';
					this.half_fix_yn='N';
					this.m_halfm_material_type = '';
