		RegHalf(){
			this.loading_yn='N';
			var err_lot ='N';
			var err_count = 'N';
			var err_count_minus = 'N';
			var err_option = 'N';
			for(var x=0;x<this.HalfItems.length;x++){
				if(this.HalfItems[x].lotno==''){
					err_lot='Y';
				}
				if(this.clm_m2_half_fit_yn != 'Y' && this.clm_m2_gui_yn != 'Y' && this.clm_m2_hom_yn != 'Y' && this.clm_m2_bolt_yn != 'Y' &&this.clm_m2_holl_yn != 'Y' 
				&& this.clm_m2_block_fit_yn != 'Y' && this.clm_m2_wrapping_yn != 'Y' && this.clm_m2_wrapping_yn != 'Y' && this.clm_m2_bending_yn != 'Y' && this.clm_m2_air_yn != 'Y'){
					err_option = 'Y';
				}
			}
			for(var x=0;x<this.MultiHalfItems.length;x++){
				if(Number(this.MultiHalfItems[x].clm_order_count)<=0 || Number(this.MultiHalfItems[x].clm_order_count)% 1!= 0){
					err_count_minus='Y';
				}
				if(Number(this.MultiHalfItems[x].clm_order_count)>Number(this.MultiHalfItems[x].clm_full_count)){
					err_count='Y';
				}
			}

			if(err_lot=='Y'){
				alert('자재를 모두 선택해 주시기 바랍니다.');
				this.loading_yn='N';
				return;
