			console.log(" width_same_yn1 : "+width_same_yn1 +" height_same_yn1 : "+height_same_yn1);
			console.log(" saw_width : "+saw_width +" saw_height : "+saw_height);
			if(this.CutleftItems.length>0){
				if(value=='1'){
					this.LossItems=[{}];
					this.LossItems.splice(0, 1);
				}else if(value=='2'){
					this.LossItems2=[{}];
					this.LossItems2.splice(0, 1);
				}else if(value=='3'){
					this.LossItems3=[{}];
					this.LossItems3.splice(0, 1);
				}
				
				for(var x=0; x < this.CutleftItems.length; x++){
					if(isNaN( this.CutleftItems[x].clm_row_count)){
						this.CutleftItems[x].clm_row_count=1;
					}
					if(Number(this.CutleftItems[x].clm_width) > Number((4.4*Number(this.CutleftItems[x].clm_row_count)).toFixed(2)) ){
						saw_width=4.4;
					}else if(Number(this.CutleftItems[x].clm_width) > Number((1.4*Number(this.CutleftItems[x].clm_row_count)).toFixed(2))){
						saw_width=1.4;
					}else if(Number(this.CutleftItems[x].clm_width) >0){
						saw_width=0;
					}
					if(Number(this.CutleftItems[x].clm_height) > Number((4.4*Number(this.CutleftItems[x].clm_sum_count)).toFixed(2)) ){
						saw_height=4.4;
					}else if(Number(this.CutleftItems[x].clm_height) > Number((1.4*Number(this.CutleftItems[x].clm_sum_count)).toFixed(2))){
						saw_height=1.4;
					}else if(Number(this.CutleftItems[x].clm_height) > 0){
						saw_height=0;
					}
					if(value=='1'){
						/*row별 톱날길이 공유 할때
						this.LossItems.push({
							clm_width: (this.CutleftItems[x].clm_width-saw_width*Number(this.CutleftItems[x].clm_row_count)).toFixed(2),
							clm_height: (this.CutleftItems[x].clm_height-saw_height*Number(this.CutleftItems[x].clm_sum_count)).toFixed(2),
							clm_thickness: this.clm_m_material_thickness,
							loss_radio:'loss',
							clm_loss_count:1
						});
						 */
						//row별 톱날길이 공유 안할때
						if(this.change_yn!='Y'){//정방향일경우
							this.LossItems.push({
								clm_width: Number((Number(this.CutleftItems[x].clm_width)-saw_width*Number(this.CutleftItems[x].clm_row_count)).toFixed(2)),
								clm_height: Number((Number(this.CutleftItems[x].clm_height)-saw_height*Number(this.CutleftItems[x].clm_sum_count)).toFixed(2)),
								clm_thickness: this.clm_m_material_thickness,
								loss_radio:'loss',
								clm_loss_count:1
							});
						}else{
							//역방향일경우
							this.LossItems.push({
								clm_width: Number((Number(this.CutleftItems[x].clm_width)-saw_width*Number(this.CutleftItems[x].clm_row_count)).toFixed(2)),
								clm_height: Number((Number(this.CutleftItems[x].clm_height)-saw_height*Number(this.CutleftItems[x].clm_sum_count)).toFixed(2)),
								clm_thickness: this.clm_m_material_thickness,
								loss_radio:'loss',
								clm_loss_count:1
							});
						}
					}else if(value=='2'){
						if(this.change_yn2!='Y'){//정방향일경우
							this.LossItems2.push({
								clm_width: Number((Number(this.CutleftItems[x].clm_width)-saw_width*Number(this.CutleftItems[x].clm_row_count)).toFixed(2)),
								clm_height: Number((Number(this.CutleftItems[x].clm_height)-saw_height*Number(this.CutleftItems[x].clm_sum_count)).toFixed(2)),
								clm_thickness: this.clm_m_material_thickness,
								loss_radio:'loss',
								clm_loss_count:1
							});
						}else{
							//역방향일경우
							this.LossItems2.push({
								clm_width: Number((Number(this.CutleftItems[x].clm_width)-saw_width*Number(this.CutleftItems[x].clm_row_count)).toFixed(2)),
								clm_height: Number((Number(this.CutleftItems[x].clm_height)-saw_height*Number(this.CutleftItems[x].clm_sum_count)).toFixed(2)),
								clm_thickness: this.clm_m_material_thickness,
								loss_radio:'loss',
								clm_loss_count:1
							});
						}
					}else if(value=='3'){
						if(this.change_yn3!='Y'){//정방향일경우
							this.LossItems3.push({
								clm_width: Number((Number(this.CutleftItems[x].clm_width)-saw_width*Number(this.CutleftItems[x].clm_row_count)).toFixed(2)),
								clm_height: Number((Number(this.CutleftItems[x].clm_height)-saw_height*Number(this.CutleftItems[x].clm_sum_count)).toFixed(2)),
								clm_thickness: this.clm_m_material_thickness,
								loss_radio:'loss',
								clm_loss_count:1
							});
						}else{
							//역방향일경우
							this.LossItems3.push({
								clm_width: Number((Number(this.CutleftItems[x].clm_width)-saw_width*Number(this.CutleftItems[x].clm_row_count)).toFixed(2)),
								clm_height: Number((Number(this.CutleftItems[x].clm_height)-saw_height*Number(this.CutleftItems[x].clm_sum_count)).toFixed(2)),
								clm_thickness: this.clm_m_material_thickness,
								loss_radio:'loss',
								clm_loss_count:1
							});
						}
					}
				}
			}else if((width_same_yn1=='N' && height_same_yn1 =='N' && this.CutInfoItems.length==0) || (width_same_yn1=='Y' && height_same_yn1 =='Y'&& this.CutInfoItems.length==0)){
				if(value=='1' && this.LossItems.length==0){
					this.LossItems.push({
						clm_width: 0,
						clm_height:0,
						clm_thickness: 0,
						loss_radio:'loss',
						clm_loss_count:0
					});
				}else if(value=='2' && this.LossItems2.length==0){
					this.LossItems2.push({
						clm_width: 0,
						clm_height:0,
						clm_thickness: 0,
						loss_radio:'loss',
						clm_loss_count:0
					});
				}else if(value=='3'&& this.LossItems3.length==0){
					this.LossItems3.push({
						clm_width: 0,
						clm_height:0,
						clm_thickness: 0,
						loss_radio:'loss',
						clm_loss_count:0
					});
				}
			}
		},
		CutaddSmallTable(){
			this.MultiCutItems.push({
				clm_m_material_id : "",
				clm_m_part_width : "",
				clm_m_part_name : "",
				clm_m_part_count : 0,
				clm_m_unit_count:1,
				clm_m_poor_count : 0,
				clm_m_part_count_readonly : false,
				clm_m_part_location_id : "",
				clm_m_part_location_position_id : "",
				clm_m_part_repository_position_id_items : [],
				clm_material_type : "",
				clm_material_width : "",
				clm_material_height : "",
				clm_material_thickness :""
			});
		},
		part_repositoryChange(index){
			this.MultiCutItems[index].clm_m_part_location_position_id = '';
			axios.get('/back/SCR003008/RepositoryPositionList',{
				params: {
		          repository_id: this.MultiCutItems[index].clm_m_part_location_id
		        }
			})
			.then(response => {
				this.MultiCutItems[index].clm_m_part_repository_position_id_items = response.data;
			})
			.catch(error => {
				console.log(error)
			});
		},
		async cutMaterialSave(){
			var tmp_total_width = 0;
			var tmp_part_total_count = 0;
			var partData = '';
			var cutData = '';
			var Error_width = 'N';
			var Error_count = 'N';
			var Error_count_minus = 'N';
			var Error_count_minus2 = 'N';

			if(this.m_cut2_material_name == ''|| this.m_cut2_material_id == ''){
				alert('자재를 선택해주시기 바랍니다.');
				return;
			}else if(this.m_cut2_user_id=='' || typeof this.m_cut2_user_id=='undefined'){
				alert('작업자를 선택해주시기 바랍니다.');
				return;
			}else if(Number(this.m_cut2_full_total) < Number(this.m_cut2_material_use_count)){
				alert('자재 가용 수량을 초과하였습니다.');
				return;
			}else if( Number(this.m_cut2_material_use_count) < 1){
				alert('자재 사용수량은 0또는 음수가 될 수 없습니다.');
				return;
			}
			
			if(this.MultiCutItems.length > 0){
				for(var x=0; x<this.MultiCutItems.length; x++){
					if(partData != ''){
						partData += '▒';
					}
					console.log(x+' '+this.MultiCutItems[x].clm_m_material_id);
					if(this.MultiCutItems[x].clm_m_material_id == '' || this.MultiCutItems[x].clm_m_part_name == ''){
						Error_count = 'Y';
					}else if(Number(this.MultiCutItems[x].clm_m_part_count) < 0){
						Error_count_minus2 = 'Y';
					}else if(this.MultiCutItems[x].clm_m_part_location_id =='' || this.MultiCutItems[x].clm_m_part_location_position_id ==''){
						Error_count_minus = 'Y';					
					}
					if(this.MultiCutItems[x].clm_m_gubun ==''){
						this.MultiCutItems[x].clm_m_gubun= ' ';
					}
					if(this.MultiCutItems[x].clm_m_poor_count==''){
						this.MultiCutItems[x].clm_m_poor_count=0;
					}
					partData += this.MultiCutItems[x].clm_m_material_id + '▦'+ this.MultiCutItems[x].clm_m_part_name +'▦'+ this.MultiCutItems[x].clm_m_part_count + '▦'+ this.MultiCutItems[x].clm_m_part_location_id+'▦'+this.MultiCutItems[x].clm_m_part_location_position_id+'▦'+this.MultiCutItems[x].clm_m_gubun+'▦'+this.MultiCutItems[x].clm_m_poor_count +'▦'+this.MultiCutItems[x].clm_m_unit_count;
					tmp_total_width = Number(tmp_total_width) + (Number(this.MultiCutItems[x].clm_m_part_count) * Number(this.MultiCutItems[x].clm_m_part_width));
					tmp_part_total_count = Number(tmp_part_total_count) + Number(this.MultiCutItems[x].clm_m_part_count);
				}
				if(Number(tmp_total_width) > (Number(this.m_cut_use_material_count) * Number(this.m_cut2_material_width))){
					Error_width = 'Y';
				}
			}else if(this.MultiCutItems.length == 0){
				alert('재단 자재를 추가해주시길 바랍니다.');
				return;
			}

			var use_yn = 'N';
			var error_use='N';
			var cnt =0;

			if(this.MultiCutItems.length > 0){
				for(var x=0; x<this.MultiCutItems.length; x++){
					if(cutData != ''){
						cutData += '▒';
					}
					var count =this.MultiCutItems[x].clm_order_count;
					if(this.MultiCutItems.length==1){
						count=this.m_cut_use_material_count;
					}
					cutData += this.MultiCutItems[x].clm_material_id + '▦'+ this.MultiCutItems[x].clm_material_order_id +'▦'+ this.MultiCutItems[x].clm_order_income_seq + '▦'+ this.MultiCutItems[x].clm_income_material_seq+'▦'+this.MultiCutItems[x].clm_material_leftover_seq+'▦'+count+'▦'+this.MultiCutItems[x].clm_full_count+'▦'+this.MultiCutItems[x].clm_material_name;					
					await axios.post('/back/Common/UseCheck', null, { params: {
						clm_joborder_id:'',
						clm_material_id:this.MultiCutItems[x].clm_material_id,
						clm_material_count: this.MultiCutItems[x].clm_full_count,
						clm_material_order_id: this.MultiCutItems[x].clm_material_order_id,
						clm_order_income_seq:this.MultiCutItems[x].clm_order_income_seq,
						clm_income_material_seq: this.MultiCutItems[x].clm_income_material_seq,
						clm_material_leftover_seq: this.MultiCutItems[x].clm_material_leftover_seq					
					}})
					.then(response => {
						if (response.status == '200') {
							if(response.data!='N'){
								cnt= x;
								error_use=response.data;
							}
						}
					})
					.catch(error => {
						console.log(error)
					})
				}
			}else{
				alert('재단 사용 자재를 추가해주시길 바랍니다.');
				this.loading_yn='N';	
				return;
			}
			if(error_use=='Y'){
				alert(this.MultiCutItems[cnt].lotno +'은(는) 이미 사용된 자재입니다.');
				this.loading_yn='N';
				return;
			}else if(error_use=='D'){
				alert(this.MultiCutItems[cnt].lotno +'은(는) 삭제된 자재입니다.');
				this.loading_yn='N';
				return;
			}else if(error_use=='U'){
				alert(this.MultiCutItems[cnt].lotno +'은(는) 수정된 자재입니다.');
				this.loading_yn='N';
				return;
			}
			if(Error_count == 'Y'){
				alert('자재가 선택되지 않았습니다.');
			}else if(Error_count_minus2 == 'Y'){
				alert('자재 사용 수량은 마이너스 및 0이 될 수 없습니다.');
			}else if(Error_width == 'Y'){
				alert('자재 길이가 입고 자재 길이 보다 길 수 없습니다.');
			}
			if(this.m_cut2_holl_size==''){
				this.m_cut2_holl_size='0';
			}else{
				this.m_cut2_holl_size=this.m_cut2_holl_size.replace(/ ∅/gi, "");
			}
			
			if( this.change_yn=='' ||  this.change_yn===null || typeof this.change_yn=='undefined'){
				this.change_yn='N';
			}
			if( this.clm_change_yn2=='' ||  this.clm_change_yn2===null || typeof this.clm_change_yn2=='undefined'){
				this.clm_change_yn2='N';
			}
			if( this.clm_change_yn3=='' ||  this.clm_change_yn3===null || typeof this.clm_change_yn3=='undefined'){
				this.clm_change_yn3='N';
			}
			axios.post('/back/SCR003008/Reg', null, { params: {
				clm_joborder_id : this.m_joborder_id,
				clm_cut_joborder_id: this.m_cut2_joborder_id,
				clm_material_order_id: this.m_cut2_material_order_id,
				clm_order_income_seq: this.m_cut2_order_income_seq,
				clm_income_material_seq: this.m_cut2_income_material_seq,
				clm_material_leftover_seq: this.m_cut2_material_leftover_seq,
				clm_material_id: this.m_cut2_material_id,
				clm_material_total_count: String(this.m_cut2_material_use_count).replace(/,/gi, ""),
				clm_part_total_count: tmp_part_total_count,
				clm_full_total_count: String(this.m_cut2_full_total).replace(/,/gi, ""),
				clm_order_bundle_count:this.m_cut2_order_bundle_count,
				clm_material_name:this.m_cut2_material_name,
				clm_material_location_id: this.m_cut2_material_location_id,
				clm_material_location_position_id: this.m_cut2_material_location_position_id,
				clm_holl_size:this.m_cut2_holl_size,
				clm_cut_joborder_user: this.m_cut2_user_id,
				clm_change_yn : this.change_yn,
				clm_change_yn2 : this.change_yn2,
				clm_change_yn3 : this.change_yn3,
				clm_user: this.user_id,
				cutData: cutData,
				partData: partData
			}})
			.then(response => {
				if (response.status == '200') {
					// 상태 업데이트
					axios.post('/back/MOB001001/JobOrderUpdateStatus', null, { params: {
						clm_joborder_id: this.m_joborder_id,
						clm_client_order_id: this.m_client_order_id,
						clm_status: '재단작업',
						clm_user: this.user_id
					}})
					.then(response => {
						if (response.status == '200') {
							this.loading_yn='N';
							Swal.fire({
								title:"정상적으로 등록 되었습니다.\n생산계획을 저장하시겠습니까?",
								text:  '',
								icon: 'question',
								showCancelButton: true,
								allowOutsideClick: false,
								confirmButtonColor: '#3085d6',
								cancelButtonColor: '#d33',
								confirmButtonText: '예',
								cancelButtonText: '아니요'
							}).then((result) => {
								if (result.isConfirmed) {
									this.ProductSave();
									this.CutRegModal = false;
									this.CutModal=false;
									// this.DetailModal = false;
									this.getList();
								}else{
									this.CutRegModal = false;
									this.CutModal=false;
									// this.DetailModal = false;
									this.getList();
								}
							})
						}
					})
				}
			})
			.catch(error => {
				console.log(error)
			})
		},
	}
}
</script>